@use 'abstract' as *;

.card {
    box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
    background-color: $white;
    min-width: rem(300);

    .card__body {
        .card__title {
            font-size: $fs-h4;
            font-weight: 900;
        }

        .card__text {
            color: $dark-gray;
        }

    }
}