.header-underline {
  position: relative;
}
.header-underline::before {
  content: "";
  position: absolute;
  height: 0.1875rem;
  background-color: #32cd32;
  top: 1.75rem;
  width: 98%;
  left: 50%;
  transform: translateX(-50%);
}

.h2 .header-underline {
  position: relative;
}
.h2 .header-underline::before {
  content: "";
  position: absolute;
  height: 0.3125rem;
  background-color: #32cd32;
  top: 2.8125rem;
  width: 60%;
  left: 50%;
  transform: translateX(-50%);
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}
.header .overlay {
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
  top: 3.875rem;
  background-image: linear-gradient(to bottom, #84e184, transparent);
  cursor: pointer;
}

.hero {
  padding: 8.125rem 0;
  display: flex;
  justify-content: end;
  align-items: center;
  background-color: #393939;
  background-image: url("../assets/images/home/hero.png");
  background-size: cover;
  background-position: center right 30%;
  background-blend-mode: multiply;
}
@media (min-width: 48rem) {
  .hero {
    padding: 10.625rem 0;
    background-position: bottom right 40%;
  }
}
.hero .hero__text {
  text-align: center;
}
@media (min-width: 48rem) {
  .hero .hero__text {
    text-align: left;
    max-width: 70%;
  }
}

.about-us .about-us__details {
  text-align: center;
}
@media (min-width: 48rem) {
  .about-us .about-us__details {
    text-align: left;
  }
}
.about-us .about-us__company {
  position: relative;
}
@media (min-width: 48rem) {
  .about-us .about-us__company::before {
    content: "";
    background-color: #32cd32;
    bottom: 0;
    height: 92%;
    position: absolute;
    right: -20%;
    transform: translatey(-4%);
    width: 4px;
  }
}

.services .service {
  transition: transform 500ms ease-in-out;
}
.services .service:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.services .service .service__image {
  border-radius: 50%;
  font-size: 2.8125rem;
  width: 50px;
  padding: 0.9375rem;
  margin: auto;
  width: 6.25rem;
  background-image: linear-gradient(-45deg, #22cccc, #32cd32);
}

.card {
