.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.nav .nav__brand .nav__brand-image {
  height: 2.1875rem;
}
@media (min-width: 48rem) {
  .nav .nav__brand .nav__brand-image {
    height: 2.5rem;
  }
}
.nav .nav__brand .nav__brand-name {
  font-weight: bold;
  font-size: 1.125rem;
  color: #5bd75b;
}
@media (min-width: 48rem) {
  .nav .nav__brand .nav__brand-name {
    font-size: 1.5rem;
  }
}
.nav .nav__toggle {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  position: relative;
  transition: background-color 1s ease-in-out;
}
.nav .nav__toggle:hover .nav__burger-line {
  background-color: #FFFFFF;
}
.nav .nav__toggle .nav__burger-line {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 0.15rem;
  border-radius: 0.3125rem;
  background-color: #32cd32;
  transition: transform 0.5s, width 0.5s;
}
.nav .nav__toggle .nav__burger-line--1 {
  transform: translate(-50%, -7px);
}
.nav .nav__toggle .nav__burger-line--2 {
  width: 100%;
}
.nav .nav__toggle .nav__burger-line--3 {
  transform: translate(-50%, 5px);
}
.nav .nav__links {
  visibility: hidden;
  height: 0;
  position: absolute;
}
@media (min-width: 48rem) {
  .nav .nav__links {
    visibility: visible;
    height: auto;
    position: relative;
  }
}
.nav .nav__links .nav__link {
  position: relative;
  transition: all 0.2s ease-in-out;
  letter-spacing: 2px;
}
.nav .nav__links .nav__link:hover {
  color: #32cd32;
}
.nav .nav__links .nav__link:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 48rem) {
  .nav .nav__links .nav__link:not(:last-child) {
    margin-right: 0.625rem;
  }
}
@media (min-width: 62rem) {
  .nav .nav__links .nav__link:not(:last-child) {
    margin-right: 1.25rem;
  }
}
.nav .nav__links .nav__link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.1875rem;
  background-color: #32cd32;
  top: 1.5625rem;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
@media (min-width: 48rem) {
  .nav .nav__links .nav__link::before {
    top: 2.25rem;
  }
}
@media (min-width: 62rem) {
  .nav .nav__links .nav__link::before {
    top: 2.4375rem;
  }
}
.nav .nav__links .nav__link--active {
  color: #32cd32;
  font-weight: bold;
}
.nav .nav__links .nav__link--active::before {
  opacity: 1;
}

.nav--open-menu {
  padding: 20px 0;
}
.nav--open-menu .nav__toggle .nav__burger-line--1 {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.nav--open-menu .nav__toggle .nav__burger-line--2 {
  width: 0;
}
.nav--open-menu .nav__toggle .nav__burger-line--3 {
  transform: translate(-50%, -50%) rotate(45deg);
}
.nav--open-menu .nav__links {
  visibility: visible;
  height: auto;
  display: flex;
  flex-flow: column;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  top: 5rem;
  background-color: #1E1B1C;
  padding: 1.25rem;
  border-radius: 5px;
  width: 15.625rem;
  align-items: center;
  z-index: 3;
  animation: opac 0.5s linear;
}

.nav__brand--shrink .nav__brand .nav__brand-image {
  height: 1.875rem;
}
.nav__brand--shrink .nav__brand .nav__brand-name {
  font-size: 1rem;
}

